import React, { useState } from "react";
import { useLocation } from "react-router-dom";
import LeavingForm from "../../../StaffTab/LeavingForm/LeavingForm";
import CommonStaffMember from "../CommonStaffMember";
import StaffMemberConfig from "../Mainbody/StaffMemberConfig";
import CertificateAuthority from "../Mainbody/CertificateAuthority/CertificateAuthority";

const CertificateConfig = () => {
  const location = useLocation();
  const [leavingform, setLeavingForm] = useState(false);
  const [student, setStudent] = useState("");
  const [toggle, setToggle] = useState(null);
     const [download, setDownload] = useState("");
  // console.info("location?.state?.onClickValue", location?.state?.onClickValue);
  return (
    <>
      {!leavingform && (
        <StaffMemberConfig
          index="26"
          sid={location?.state?.sid ?? ""}
          onClickValue={location?.state?.onClickValue ?? ""}
        >
          <CertificateAuthority
            instituteId={location?.state?.onClickValue?.institute}
            carryParentState={location?.state}
            leavingform={leavingform}
            setLeavingForm={setLeavingForm}
            student={student}
            setStudent={setStudent}
            download={download}
            toggle={toggle}
            setToggle={setToggle}
            setDownload={setDownload}
          />
        </StaffMemberConfig>
      )}

      {leavingform && (
        <CommonStaffMember>
          <LeavingForm
            setLeavingForm={setLeavingForm}
            student={student}
            setStudent={setStudent}
            download={download}
            setDownload={setDownload}
            toggle={toggle}
            setToggle={setToggle}
            instituteId={location?.state?.onClickValue?.institute}
            viewAs="DESIGNATION_SIDE"
          />
        </CommonStaffMember>
      )}
    </>
  );
};

export default CertificateConfig;
